            Competences: require("./competences.vue").default,
            Experience: require("./experience.vue").default,
            Qualite: require("./qualite.vue").default
        },
        methods: {
            showDiv: function (id) {
                var div = document.getElementById(id);
                if (div.style.display === "none") {
                    div.style.display = "block";
                } else {
                    div.style.display = "none";
                }
            }
        }
    }
</script>

